.index-manage-grid {
    display: grid;
    grid-template-columns: 310px 310px 200px 420px;
    grid-gap: 20px;

    & > .last-news {
        grid-column: 4/5;
        grid-row: 1/2; }

    & > :nth-child(2) {
        grid-column: 1/4;
        grid-row: 1/2; }

    & > :nth-child(3) {
        grid-column: 1/2;
        grid-row: 2/3; }

    & > :nth-child(4) {
        grid-column: 2/3;
        grid-row: 2/3; }

    & > :nth-child(5) {
        grid-column: 3/5;
        grid-row: 2/3; } }



.todo-layout {
    display: grid;
    grid-template-columns: repeat(4, 310px);
    grid-gap: 20px;

    & > .grid-publication_6-col {
        grid-column: 1/3;
        grid-row: 1/2; }

    & > .cinema-widget {
        grid-column: 4/5;
        grid-row: 1/2; } }
